import React, { Component } from 'react';
import MainHead from './MainHead.js';
import './App.scss';
import Introduction from './Introduction.js';
import ShrineDescription from './ShrineDescription.js';
import ShrinePresentation from './ShrinePresentation.js';



class App extends Component {
  state = {
    load: false
  }
  handleAsyncLoad = () => {
    !this.state.load && this.setState(() => ({
      load: true
    }));
  }
  componentDidMount() {
    window.addEventListener('scroll', this.handleAsyncLoad)
  }
  render() {
    const { load } = this.state;
    return (
      <div className="App">
        <MainHead click={this.handleAsyncLoad} />
        <Introduction />
        <ShrinePresentation />
        <ShrineDescription asyncLoading={load} />
      </div>
    );
  }
}

export default App;